.sidebar
{
    flex:3;
    margin:20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 50%;
    flex-direction:column;
    align-items:center;
}
.sidebarItem
{
 display:flex;
 flex-direction:column;
 align-items:center;
}
.sidebarItems
{
 display:flex;
 flex-direction:column;
 align-items:center;
}
.sidebarTitle
{
    margin: 10px;
   padding: 10px;
   width:80%;
   border-top:1px solid #a7a4a4;
   border-bottom: 1px solid #a4a7a7;
   font-family:'Varela Round' ,sans-serif;
   font-size:12px;
   color:#222;
   font-weight: 600;
   line-height:20px;
   text-align:center;
}
.sidebar>img{
    margin-top:50px;
}
.sidebar>p{
    padding:30px;
}
.sidebarList
{
    list-style:none;
    margin-bottom:30px;
}
.sidebarListItem
{
    display:inline-block;
    width:50%;
    margin-top: 10px;
    cursor:pointer;
}
.sidebarSocial
{
    margin-top:15px;
    width:250px;
    display:flex;
    align-items:center;
    justify-content:center;
}
.sidebarIcon
{
    font-size:20px;
    margin-left:10px;
    cursor:pointer;
}